import { QueryList } from '@angular/core';
import { ColumnComponent } from './column.component';

/**
 * @hidden
 */
export class ColumnsContainer {
    public lockedColumns: QueryList<ColumnComponent> = new QueryList<ColumnComponent>();
    public nonLockedColumns: QueryList<ColumnComponent> = new QueryList<ColumnComponent>();

    constructor(private columns: Function) { }

    public refresh(): void {
        this.lockedColumns.reset(this.columns().filter(x => x.locked === true));
        this.nonLockedColumns.reset(this.columns().filter(x => !x.locked));
    }
}



